import React from 'react';
import { PureRenderMixin } from 'react/addons';
import { map } from 'lodash-compat';
import InputNumber from './InputNumber';
import InputSelect from './InputSelect';
import MoreInfo from './MoreInfo';

var { PropTypes } = React;

var ProductDetail = React.createClass({
  mixins: [PureRenderMixin],
  getDefaultProps() {
    return {
      id: '',
      title: '',
      album: [{
        thumb: 'http://placekitten.com/g/50/50',
        image: 'http://placekitten.com/g/300/300'
      }, {
        thumb: 'http://placekitten.com/g/50/50',
        image: 'http://placekitten.com/g/300/300'
      }, {
        thumb: 'http://placekitten.com/g/50/50',
        image: 'http://placekitten.com/g/300/300'
      }, {
        thumb: 'http://placekitten.com/g/50/50',
        image: 'http://placekitten.com/g/300/300'
      }, {
        thumb: 'http://placekitten.com/g/50/50',
        image: 'http://placekitten.com/g/300/300'
      }],
      points: 0,
      dollars: 0,
      mixed: {
        points: 0,
        dollars: 0
      },
      layaway: {
        dollars: 3333,
        installments: 3
      },
      notes: ''
    };
  },
  _renderThumb(item, i) {
    return <li key={i} ><a href="#"><img src={item.thumb} /></a></li>;
  },
  render() {
    var { className = '', children = [],
          id, title, album, points, dollars,
          mixed, layaway, notes } = this.props;
    return (
      <div className={`yfs-comp product-detail ${className}`}>
        <div className="left">
          <figure className="image">
            <img src={album[0].image} />
          </figure>
          <ol className="thumbs">
            {map(album, this._renderThumb)}
          </ol>
        </div>
        <div className="right">
          <h2 className="title">{title}</h2>
          <dl className="price">
            <dt></dt>
            <dd><strong>{mixed.points}</strong> + $<strong>{mixed.dollars}</strong></dd>
            <dt></dt>
            <dd>{points}</dd>
            <dt></dt>
            <dd>${dollars}</dd>
            <dt></dt>
            <dd>${layaway.dollars}/({layaway.installments}0)</dd>
          </dl>
          <div className="inputs">
            <InputNumber />
            <InputSelect placeholder="" />
            <InputSelect placeholder="" />
          </div>
          <div className="notes">{notes}</div>
          <div className="actions">
            <a href="#"></a>
            <a href="#"></a>
            <a href="#"></a>
          </div>
        </div>
        <MoreInfo />
        {children}
      </div>
    );
  }
});

export default ProductDetail;




